.chat {
  flex: 0.65;
  display: flex;
  flex-direction: column;
}

.chat__header {
  padding: 15px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid lightgray;
}

.chat__headerInfo {
  align-items: center;
  flex: 1;
}

.chat__headerInfo {
  margin-left: 10px;
  flex: 1;
}

.chat__headerInfo > h3 {
  font-weight: 500;
  margin-bottom: 3px;
}

.chat__headerInfo > p {
  color: gray;
}

.chat__headerRight {
  min-width: 100px;
  display: grid;
  place-items: center;
  display: flex;
}

.chat__body {
  background-image: url("./../../assets/whatsapp.png");
  flex: 1;
  background-repeat: repeat;
  padding: 30px;
  background-position: center;
  overflow: scroll;
}

/* For webkit browsers (Chrome, Safari) */
.chat__body::-webkit-scrollbar {
  width: 0; /* Remove vertical scrollbar */
  height: 0; /* Remove horizontal scrollbar */
}

/* Cross-browser solution for Firefox */
.chat__body {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer/Edge */
}

.chat__message {
  position: relative;
  background-color: white;
  border-radius: 10px;
  width: fit-content;
  font-size: 16px;
  padding: 10px;
  margin-bottom: 30px;
}
.chat__message > span {
  text-wrap: nowrap;
}

.chat__name {
  position: absolute;
  top: -15px;
  font-weight: 800;
  font-size: xx-small;
}

.chat__timestamp {
  margin-left: 10px;
  font-size: xx-small;
}

.chat__receiver {
  margin-left: auto;
  background-color: #dcf8dc;
}

.chat__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 62px;
  border-top: 1px solid lightgray;
}

.chat__footer > form {
  flex: 1;
  display: flex;
}

.chat__footer > form > input {
  flex: 1;
  border-radius: 30px;
  padding: 10px;
  border: none;
}

.chat__footer > form > button {
  display: none;
}

.chat__footer > svg {
  padding: 10px !important  ;
  color: gray !important;
}
