.sidebar {
  flex: 0.35;
  display: flex;
  flex-direction: column;
}

.sidebar__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-right: 1px solid lightgray;
}

.sidebar__header_right > * {
  margin: 10px 5px;
  cursor: pointer;
  font-size: 24px;
}

.sidebar__search {
  display: flex;
  align-items: center;
  background-color: #f6f6f6;
  height: 39px;
  padding: 10px;
}

.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 35px;
  border-radius: 20px;
}

.sidebar__searchContainer > input {
  border: none;
  margin-left: 10px;
  width: 100%;
  outline-width: 0;
  position: relative;
}

.sidebar__searchContainer > .MuiSvgIcon-root {
  color: gray;
}

.sidebar__add {
  color: gray !important;
}

.sidebar__add {
  cursor: pointer;
}

.sidebar_chats {
  flex: 1;
  overflow: scroll;
  background-color: white;
}

/* For webkit browsers (Chrome, Safari) */
.sidebar_chats ::-webkit-scrollbar {
  width: 0; /* Remove vertical scrollbar */
  height: 0; /* Remove horizontal scrollbar */
}

/* Cross-browser solution for Firefox */
.sidebar_chats {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer/Edge */
}
